import { clocks } from './time.js'
import toolBrowser from 'common/js/tool.browser.js'

export default {
  data(){
    return{
      cStatus: false,
      ctime: {
        h: '22:',
        m: '36',
        d: 'PM'
      },
      cnotice: '听书，听音乐，听演播，听人情冷暖！',
      content: []
    }
  },
  methods:{
    initClockData(){
      this.content = clocks;
    },
    initTimeData(){
      let time = toolBrowser.getTime();

      this.ctime.h = time.hours+':';
      this.ctime.m = time.minute;
      this.ctime.d = time.hours*1 < 12 ? 'AM' : 'PM';

      this.cnotice = clocks[0].notice;
    },
    initEvents(){
      let toggle = $('.navBtn');
      let liWrap = $('.tf-lists');
      let hh = $('.citem').height();
      

      toggle.click((e)=>{

        let h = this.content.length*hh;

        if(!this.cStatus){
          liWrap.css({
            height: h,
            opacity: 1
          });
        }else{
          liWrap.css({
            height: 0,
            opacity: 0
          });
        }

        this.cStatus = !this.cStatus;
      })
    },
    initHeight(){
      let liWrap = $('.tf-lists');
      let hh = $('.citem').height();
      let h = this.content.length*hh;

      liWrap.css('height',h);
    },
    removeClock(index){
      this.content.splice(index,1);
      this.initHeight();
      //console.log(index);
    },
    //check clock time 
    checkime(ct,clocks){
      var ctc = '';
      var c1 = 0;
      var c2 = 0;

      for(var i=0;i<clocks.length;i++){
        ctc = clocks[i].time.substr(0,5);
        c1 = ct.split(':')[0]*60 + ct.split(':')[1]*1;
        c2 = ctc.split(':')[0]*60 + ctc.split(':')[1]*1;
        if(c1 == c2){
          //开始减去这个时间
          clocks.splice(i,1);
          this.initHeight();
          this.auidoPlay();
          break;
        }else if(c2 < c1){
          clocks.splice(i,1);
        }
      }

      this.cnotice = clocks[0].notice;
    },
    auidoPlay(){
      $('#audioElem').get(0).load();
      $('#audioElem').on('loadedmetadata',()=>{
        $('#audioElem').get(0).play();
      });
    },
    //init requestAnimationFrame timer
    initTimer(){
      let This = this;
      let time = '';

      function fn(){
        var timer = setTimeout(()=>{
          //requestAnimationFrame(fn);
          This.initTimeData();
          time = This.ctime.h+This.ctime.m;
          This.checkime(time,This.content);
          fn();
        },20000);
      }

      fn();
    }
  },
  created(){
    this.initClockData();
    this.initTimeData();
  },
  mounted(){
    this.initEvents();
    this.initTimer();
  }
};


