<template>
  <div class="time-container" ref="container">
      <section class="sContainer">
        <div class="wrapper">
          <audio :src="require('./audio/audio.aac')" id="audioElem"></audio>
          <section class="theader">
            <Icon type="close" class="closeBtn"></Icon>
            <Icon type="android-notifications" class="noticeIcon"></Icon>
            <span class="tnotice-text">{{cnotice}}</span>
            <section class="time-content">
              <span class="tc-hour">{{ctime.h}}</span>
              <span class="tc-minute">{{ctime.m}}</span>
              <span class="tc-day">{{ctime.d}}</span>
            </section>
          </section>

          <section class="tfooter">
            <ul class="tf-lists">
              <li class="citem" v-for="item,index in content">
                <Icon type="android-alarm-clock" class="android-alarm-clock"></Icon>
                <span class="alarm-clock-time">{{item.time}}</span>
                <span class="alarm-clock-txt">{{item.notice.length < 15 ? item.notice : item.notice.substr(0,15)+'...'}}</span>
                <Icon type="android-close" class="listCloseBtn" @click.stop="removeClock(index)"></Icon>
              </li>
            </ul>
            <div class="panel">
              <Icon type="navicon" class="navBtn"></Icon>
            </div>
          </section>

        
        </div>
      </section>
  </div>
</template>

<script scoped>
  import time from './js/content.js';
  export default time;
</script>

<!-- Add "scoped" attribute to limit CSS to this component only @import './login.styl' -->
<style scoped lang="stylus">
    @import './stylus/time.styl'
</style>


